@import url(https://fonts.googleapis.com/css?family=Kaushan+Script|Open+Sans:400,700|PT+Serif:400,700|Roboto:400,700);
@import url(https://fonts.googleapis.com/css?family=Kaushan+Script|Open+Sans:400,700|PT+Serif:400,700|Roboto:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (max-width: 480px) {
    .mainForm {
        width: 100%;
        text-align: left;
        margin-right: 5vw !important;
    }
    
    div.section {
        padding-top: 2vh;
    }
    
    .textField {
        height: 20px;
        width: 50vw;
        font-family: 'Open Sans', serif;
        font-size: 15px;
      }
    
    .submit {
        font-family: 'Open Sans', sans-serif;
        font-size: 15px;
        padding: 1vh 1vw;
        margin-top: 2vh;
        background-color: #4885ed;
        color: white;
        margin-left: 40%;
    }
    
    label {
        font-weight: 700;
    }
}

@media screen and (min-width: 481px) {
    .mainForm {
        width: 100%;
        text-align: right;
        margin-right: 5vw !important;
    }
    
    div.section {
        padding-top: 2vh;
    }
    
    .textField {
        height: 20px;
        width: 30vw;
        font-family: 'Open Sans', serif;
        font-size: 15px;
      }
    
    .submit {
        font-family: 'Open Sans', sans-serif;
        font-size: 15px;
        padding: 1vh 1vw;
        margin-top: 2vh;
        background-color: #4885ed;
        color: white;
    }
    
    label {
        font-weight: 700;
    }
}

@media screen and (max-width: 480px) {
  body {
    background-color: #FBFBFB;
    max-height: 100vh;
    margin: 3vh 3vw 3vh 3vw;
  }
  
  h1 {
    font-family: 'Kaushan Script', cursive, serif;
    color: #4885ed;
  }
  
  
  .body {
    font-family: 'Open Sans', serif;
    font-size: 15px;
  }
  
  
  .main {
    flex: 1 0 auto;
    display: grid;
    grid-template-rows: auto auto;
    grid-row-gap: 5vw;
    /* grid-template-rows: auto auto; */
  } 
  
  .credit {
    flex-shrink: 0;
    margin-top: 20px;
    text-align: center;
  }
  
  .katie {
    color: #4885ed;
    font-weight: 700;
  }
  
  a {
    text-decoration: none;
  }
}

@media screen and (min-width: 481px) {
  body {
    background-color: #FBFBFB;
    max-height: 100vh;
    overflow: hidden;
    margin: 3vh 3vw 3vh 3vw;
    display: flex;
    flex-direction: column;
  }
  
  h1 {
    font-family: 'Kaushan Script', cursive, serif;
    color: #4885ed;
  }
  
  
  .body {
    font-family: 'Open Sans', serif;
    font-size: 15px;
  }
  
  
  .main {
    flex: 1 0 auto;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 5vw;
    /* grid-template-rows: auto auto; */
  } 
  
  .credit {
    flex-shrink: 0;
    margin-top: 20px;
    text-align: center;
  }
  
  .katie {
    color: #4885ed;
    font-weight: 700;
  }
  
  a {
    text-decoration: none;
  }
}

